<template>
  <div class="nts-card">
    <div class="mask">
      <div class="download" @click="printNTS">
        
      </div>

    </div>
    <!-- <div>{{index}}</div>
    <div class="block">{{details.examModel.title}}</div>
    <div class="block">{{details.examModel.examType.msg}}</div>
    <div class="block">{{details.examModel.startTime | parseTime}}</div>
    <div class="block" @click="printNTS"></div> -->

    <div class="title">{{details.examModel.title}}</div>
    <img :src="details.identificationPhoto" class="avatar" />
    <div class="table">
      <table>
        <tr>
          <td style="width:90px"></td>
          <td style="width:200px">{{details.applyName}}</td>
          <td style="width:90px"></td>
          <td style="width:200px">{{details.examNumber}}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{details.examRoom}}</td>
          <td></td>
          <td>{{details.idNumber}}</td>
        </tr>
        <tr>
          <td></td>
          <td>{{details.examModel.startTime | parseTime}}</td>
          <td></td>
          <td>{{details.examModel.endTime | parseTime}}</td>
        </tr>
      </table>
    </div>
    <div class="tip">
      
    </div>
    <p class="word">1</p>
    <p class="word">2 </p>
    <p class="word">3 </p>
    <p class="word">4 </p>
    <p class="word">5 </p>
  </div>
</template>
<script>
import { parseTime } from '@/utils/time'
export default {
  props: {
    details: Object,
    index: Number
  },
  filters: {
    parseTime (val) {
      return parseTime(val, '{y}-{m}-{d} {h}:{i}') || '--'
    }
  },
  computed: {

  },
  methods: {
    printNTS () {
      // let link = document.createElement('a')
      let userId = JSON.parse(localStorage.getItem('userInfo')).sub
      // link.href = `http://localhost:2020/word/export-word?examId=${this.details.examId}&userId=${userId}`
      // // link.download = '123.doc'
      // link.click()
      let params = {
        examId: this.details.examId,
        userId: userId
      }
      this.$store.dispatch('printNTS', params).then(res => {
        let content = res.data
        // a
        let elink = document.createElement("a");
        // 
        elink.style.display = "none";
        let blob = new Blob([content]);
        elink.href = URL.createObjectURL(blob);
        elink.download = this.details.examModel.title + '.doc'
        document.body.appendChild(elink);
        elink.click();
        document.body.removeChild(elink);

      })
    }
  },
}
</script>

<style lang="scss" scoped>
.nts-card {
  position: relative;
  padding: 20px;
  // height: 40px;
  // justify-content: space-between;
  width: 580px;
  // height: 682px;
  background-color: #fff;
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    display: none;
    color: rgb(55, 102, 230);
    .download {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      // color: rgb(190, 22, 36);
      color: #fff;
      font-weight: 600;
      font-size: 20px;
      cursor: pointer;
    }
  }

  &:hover .mask {
    display: block;
  }
  .avatar {
    margin: 20px auto;
    width: 120px;
    height: 160px;
    background-color: bisque;
  }
  .table {
    table,
    td,
    th {
      border: 1px solid black;
    }

    table {
      width: 100%;
      border-collapse: collapse;
      tr {
        .lebel {
          width: 40px;
        }
      }
    }
  }

  .word {
    text-align: left;
    line-height: 1.8rem;
  }
}
</style>
